import React from 'react';
import s from './Dialogs.module.css';
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../utils/validators/validator_1";
import {Element} from "../common/FormControls/FormControls";

let maxLength20 = maxLengthCreator(20);
let Textarea = Element('textarea');

const Dialogs = (props) => {
    let dialogElements = props.dialogPage.dialogs.map( d => <DialogItem  name={d.name} key={d.id} id={d.id}/>)
    let messageElements = props.dialogPage.messages.map( m => <Message message={m.message} key={m.id} />)

    let addNewMessage = (value) => {
        props.sendMessage(value.newMessageText);
    }

    return(
        <div className={s.dialogs}>
            <div className={s.dialogItems}>
                {dialogElements}
            </div>
            <div className={s.messages}>
                <div>{messageElements}</div>
                <AddMessageFormRedux onSubmit={addNewMessage}/>
            </div>
        </div>
    )
}

const AddMessageForm = (props) => {

    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} validate={[required, maxLength20]} name={'newMessageText'} placeholder='Enter Your Message'/>
            </div>
            <div>
                <button>Add Message</button>
            </div>
        </form>
    )
}

const AddMessageFormRedux = reduxForm({form: 'dialogAddMessageForm'})(AddMessageForm)

export default Dialogs;